module.exports = " <img src=http://casarover.oss-cn-hangzhou.aliyuncs.com/image/aboutus.jpg alt=\"\" class=banner _v-26cdf4ae=\"\"> <div class=main _v-26cdf4ae=\"\"> <div class=part id=about-us _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">--A--</h2> <p _v-26cdf4ae=\"\">关于我们</p> <div class=case _v-26cdf4ae=\"\"> <p _v-26cdf4ae=\"\">探庐者，英文logo是CaSa Rover ，含义是去探访一个远方的家。也意味着心灵去旅行，去寻找你梦中的那一片纯净之地。探庐者代表着一种探索发现美的意境，代表着一种返璞归真的生活态度。</p> <p _v-26cdf4ae=\"\">“庐”字上的一抹云，又给人无尽的想象空间，仿佛是行云流水的云彩，又或是层峦叠嶂的山脉，既有一种超脱飘然的悠闲自在，也有一种天马行空的无拘无束。</p> <p _v-26cdf4ae=\"\">我们是一群爱生活，爱自由，爱旅行的年轻人，在这样一个互联网创业平台上，去追逐自己的年轻梦想。</p> <p _v-26cdf4ae=\"\">远庐致力于成为最大最全的民宿信息汇聚平台，力求探索最有特色的民宿，带大家发现简约而不简单的生活，体验与众不同却曾梦中追逐过的快乐。</p> </div> </div> <div class=part id=brand-culture _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">--B--</h2> <p _v-26cdf4ae=\"\">品牌文化</p> <div class=case _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">自然</h2> <p _v-26cdf4ae=\"\">一：人与自然的完美融合，人从大自然丛林中走出已经很多年了，但是我们的本性还是亲近自然的，用我们的身体的感知器官和自然再次有了原始的链接。 二：心性上的自然而然，万事顺势而为，道法自然。讲究的是人的一种心态，与世无争，超然脱俗，顺其自然的意境。</p> <h2 _v-26cdf4ae=\"\">简约</h2> <p _v-26cdf4ae=\"\">一切都是那么简单明了，因为简单包含的是一种能量的积蓄，无就是有，less is more。对人生的理解，一切从简单出发，才会获得更多的快乐。</p> <h2 _v-26cdf4ae=\"\">美好</h2> <p _v-26cdf4ae=\"\">对美的一种向往，不管是体验过的或是未体验过的，都会有一种无限的期待，那是一种对美好的追求，也是我们生活中的一种期盼，未来是能够拥有美好的，所谓岁月静好，无改当初。</p> <h2 _v-26cdf4ae=\"\">探索</h2> <p _v-26cdf4ae=\"\">一种对待生活的态度，要能发现生活中不一样的美，去体验一种特别的生活方式，从而能完美的诠释人生的丰富多彩。</p> <h2 _v-26cdf4ae=\"\">乐享</h2> <p _v-26cdf4ae=\"\">探索后的获得，应该也只有分享后让更多的人了解，才会更具有获得的价值，每个人活着的意义不在于得到多少，而在于能让人多少得到。分享后的快乐是一种人性的升华，是一种对生活更深刻的理解。</p> </div> </div> <div class=part id=charge-standard _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">--C--</h2> <p _v-26cdf4ae=\"\">合作推广</p> <div class=case _v-26cdf4ae=\"\"> <p _v-26cdf4ae=\"\">头条：8000元，阅读量：2万+</p> <p _v-26cdf4ae=\"\">二条：5000元，阅读量：15000</p> <p _v-26cdf4ae=\"\">三条：3500元，阅读量：10000</p> <p _v-26cdf4ae=\"\">不群发推广单篇：2000/篇</p> <p _v-26cdf4ae=\"\">联合外媒同步发，价格等同头条价格。</p> <p _v-26cdf4ae=\"\">如需拍摄视频，价格另议。</p> <p _v-26cdf4ae=\"\">详情请联系微信号：sonoffeng</p> </div> </div> <div class=part id=personal-customized _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">--D--</h2> <p _v-26cdf4ae=\"\">私人定制</p> <div class=case _v-26cdf4ae=\"\"> <p _v-26cdf4ae=\"\">1、针对顾客</p> <p _v-26cdf4ae=\"\">我们是专业的民宿信息汇聚平台，您可以在我们的平台上找到各类最具特色的、最合乎您需求的民宿。并且我们可以帮您制定旅游路线，可以根据您的要求，让您体验你一直梦寐以求的生活。</p> <p _v-26cdf4ae=\"\">2、针对客户</p> <p _v-26cdf4ae=\"\">我们的不只是提供民宿信息，我们也针对想要拥有属于自己的民宿的小伙伴们提供各项服务，我们提供从民宿设计到推广的所有环节的服务，只要你有想开民宿的想法和实现你的想法的决心，我们就能帮你实现它。</p> </div> </div> <div class=part id=business-cooperation _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">--E--</h2> <p _v-26cdf4ae=\"\">商务合作</p> <div class=case _v-26cdf4ae=\"\"> <p _v-26cdf4ae=\"\">一、我们的平台上有各类民宿信息，可以根据您的需求提供给您最适合的民宿，并提供民宿详情及旅游路线等相关信息。</p> <p _v-26cdf4ae=\"\">二、我们提供民宿设计到推广的所有环节的服务，只要你有想开民宿的想法和实现你的想法的决心，我们就能帮你实现它。</p> </div> </div> <div class=part id=contact _v-26cdf4ae=\"\"> <h2 _v-26cdf4ae=\"\">--F--</h2> <p _v-26cdf4ae=\"\">联系我们</p> <div class=case _v-26cdf4ae=\"\"> <p _v-26cdf4ae=\"\">电话：13958056604</p> <p _v-26cdf4ae=\"\">邮箱：service@casarover.com</p> <p _v-26cdf4ae=\"\">公司地址：浙江省杭州市西溪街道文二路188号16号楼</p> </div> </div> </div> ";


